.card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    width: 100%;
}

.number {
    font-size: 48px;
    color: blue;
}

.details {
    flex-grow: 1;
    padding-left: 10px;
}

.details h2 {
    margin: 0;
    font-size: 24px;
}

.details p {
    margin: 0;
    color: #555;
}

.actions {
    display: flex;
    flex-direction: column;
}

